/* Legal */
#legal {
    width: 980px;
    /* (980/1425)*100 = 68.77192982456141 */
    width: 68.77192982456141vw;
    margin: 0 auto;
    padding: 34px 0 21px;
    padding: 4.427083333333334vh 0 2.734375vh;
    color: #86868b;
}
#legal h3 {
    margin-bottom: 7px;
    margin-bottom: 0.9114583333333334vh;
    padding-bottom: 8px;
    padding-bottom: 1.0416666666666665vh;
    border-bottom: 1px solid #d2d2d7;
}
#legal h3>a {
    color: #06c;
}
#legal>div {
    display: flex;
    justify-content: space-between;
    margin-top: 5px;
    margin-top: 0.6510416666666667vh;
}
#legal>div div:first-child {
    margin: 5px 30px 0 0;
    margin: 0.6510416666666667vh 2.1052631578947367vw 0 0;
}
#legal>div div:first-child a {
    color: #515154;
}
#legal>div div:nth-child(2) {
    color: #515154;
    margin-left: -288px;
    /* (288/1425)*100 = 20.210526315789473 */
    margin-left: -20.210526315789473vw;
}
#legal>div div:nth-child(2) a {
    border-right: 1px solid #d2d2d7;
    margin-top: 5px;
    margin-top: 0.6510416666666667vh;
    margin-right: 7px;
    margin-right: 0.4912280701754386vw;
    padding-right: 10px;
    padding-right: 0.7027406886858749vw;
    display: inline-block;
    white-space: nowrap;
}
#legal>div div:nth-child(2) a:last-child {
    border-right: none;
}
#legal>div div:last-child {
    margin-top: 5px;
    margin-top: 0.6510416666666667vh;
}

#legal span:after {
    content: "\f054";
    font-family: "Font Awesome 5 Free";
    font-weight: 900;
    font-size: 13px;
    /* font-size: 0.8125rem; */
    color: #06c;
    padding-left: 5px;
    padding-left: 0.6510416666666667vh;
    display: inline-block;
}

/* select 스타일 */
select {
    border: none;
    outline: none;
    appearance: none;
    cursor: pointer;
}