/* Section 01 */
.promos {
    background-color: #fff;
    padding: 12px 12px;
    /* (12/768)*100 */
    /* (12/1425)*100 = 0.8421052631578947 */
    padding: 1.5625vh 0.8421052631578947vw;
}
.promos ul {
    /* 플렉스 */
    display: flex;
    justify-content: space-between;
}
/* Promotion Left */
.promos li:first-child {
    width: 694.5px;
    /* (694.5/1425)*100 = 48.73684210526316 */
    width: 48.73684210526316vw;
    height: 574px;
    /* (574/768)*100 */
    height: 74.739583333333333vh;
    background-image: linear-gradient(320deg, rgb(10, 96, 216) 0%, rgb(81, 214, 254) 100%);
    background-origin: padding-box;
    padding: 52px 0 0 0;
    /* (52/768)*100 */
    padding: 6.770833333333333vh 0 0 0;
    text-align: center;
} 
.promos li:first-child h4 {
    font-size: 32px;
    /* (32/768)*100 */
    font-size: 4.166666666666667vh;
    color: #fafafa;
    font-weight: 600;
    line-height: 44px;
    /* (44/768)*100 */
    line-height: 5.729166666666667vh;
}
/* 리액트 사용 추가 CSS */
.promos li:first-child div img {
    width: 200px;
    /* (200/1425)*100 = 14.035087719298245 */
    width: 14.035087719298245vw !important;
}

.promos li:first-child h5 {
    font-size: 21px;
    /* (21/768)*100 */
    font-size: 2.734375vh;
    color: #fafafa;
    font-weight: 300;
    margin-top: 4px;
    line-height: 26px;
    /* (26/768)*100 */
    line-height: 3.385416666666667vh;
}
.promos h5+div {
    margin-top: 120px;
    /* (120/768)*100 */
    margin-top: 15.625vh;
}

/* Promotion Right */
.promos .last {
    width: 694.5px;
    width: 48.73684210526316vw;
    height: 574px;
    height: 74.739583333333333vh;
    /* 리액트 수정 */
    background-position: 50% 50%;
    background-size: auto 100%;
    background-origin: padding-box;
    text-align: center;
}
.promos li:last-child h4 {
    font-size: 34px;
    /* (34/768)*100 */
    font-size: 4.427083333333333vh;
    color: #fafafa;
    font-weight: 600;
    line-height: 44px;
    /* (44/768)*100 */
    line-height: 5.729166666666667vh;
    padding-top: 380px;
    /* (380/768)*100 */
    padding-top: 49.479166666666667vh;
}
.promos li:last-child h5 {
    font-size: 18px;
    /* (18/768)*100 */
    font-size: 2.34375vh;
    color: #06c;
    font-weight: 400;
    line-height: 24px;
    /* (24/768)*100 */
    line-height: 3.125vh;
    padding-top: 16px;
    /* (16/768)*100 */
    padding-top: 2.083333333333333vh;
}
/* 폰트어썸 가상클래스 적용 */
.promos li:last-child h5 a:first-child::after {
    content: "\f054";
    font-family: "Font Awesome 5 Free";
    font-weight: 900;
    font-size: 13px;
    /* (13/768)*100 */
    font-size: 1.692708333333333vh;
    color: #06c;
    padding-left: 8px;
    display: inline-block;
}
/* 리액트 추가 */
/* .promos li:last-child {
    margin-left: 18px;
    margin-left: 1.263157894736842vw;
} */

.promos li:last-child h5 a:last-child::after {
    content: "\f144";
    font-family: "Font Awesome 5 Free";
    font-weight: 400;
    font-size: 18px;
    /* (18/768)*100 */
    font-size: 2.34375vh;
    color: #06c;
    padding-left: 8px;
    display: inline-block;
}