#footer {
    font-size: 12px;
    line-height: 1.3334;
    font-weight: 400;
    background-color: #f5f5f7;
    color: #515154;
    min-width: 1024px;
    /* (1024/1425)*100 = 71.85964912280701 */
    min-width: 71.85964912280701vw;
    padding-top: 25px;
    /* (25/768)*100 = 3.2552083333333335 */
    padding-top: 3.2552083333333335vh;
}


/* Legal */
#legal {
    width: 980px;
    margin: 0 auto;
    padding: 34px 0 21px;
    color: #86868b;
}
#legal h3 {
    margin-bottom: 7px;
    padding-bottom: 8px;
    border-bottom: 1px solid #d2d2d7;
}
#legal h3>a {
    color: #06c;
}
#legal>div {
    /* 플렉스 */
    display: flex;
    justify-content: space-between;
    margin-top: 5px;
}
#legal>div div:first-child {
    margin: 5px 30px 0 0;
}
#legal>div div:first-child a {
    color: #515154;
}
#legal>div div:nth-child(2) {
    color: #515154;
    margin-left: -288px;
}
#legal>div div:nth-child(2) a {
    border-right: 1px solid #d2d2d7;
    margin-right: 7px;
    padding-right: 10px;
    display: inline-block;
    margin-top: 5px;
}
#legal>div div:nth-child(2) a:last-child {
    border-right: none;
}
#legal>div div:last-child {
    margin-top: 5px;
}
#legal span::after {
    content: "\f054";
    font-family: "Font Awesome 5 Free";
    font-weight: 900;
    font-size: 13px;
    color: #06c;
    padding-left: 5px;
    display: inline-block;
}

/* select 스타일 */
select {
    border: none;
    outline: none;
    appearance: none;
    cursor: pointer;
}