.apps {
    background-color: #fff;
    padding: 12px 12px 0;
    /* (12/768)*100 */
    /* (12/1425)*100 = 0.8421052631578947 */
    padding: 1.5625vh 0.8421052631578947vw 0;
}
.apps ul {
    display: flex;
    flex-flow: row wrap;
    justify-content: space-between;
}
/* 앱스1 */
.apps li {
    width: 702px;
    /* (702/1425)*100 = 49.26315789473684 */
    width: 49.26315789473684vw;
    width: 694.5px;
    /* (694.5/1425)*100 = 48.73684210526316 */
    width: 48.73684210526316vw;
    height: 574px;
    /* (574/768)*100 */
    height: 74.739583333333333vh;
    background-image: linear-gradient(320deg, rgb(184, 201, 228) 0%, rgb(231, 244, 248) 100%);
    padding: 52px 0 0;
    /* (52/768)*100 */
    padding: 6.770833333333333vh 0 0;
    margin-bottom: 12px;
    /* (12/768)*100 */
    margin-bottom: 1.5625vh;
    text-align: center;
}
.apps li h4 {
    font-size: 40px;
    /* (40/768)*100 */
    font-size: 5.208333333333333vh;
    color: #1d1d1f;
    font-weight: 600;
    line-height: 44px;
    /* (44/768)*100 */
    line-height: 5.729166666666667vh;
}
.apps li h5 {
    font-family: "SF Pro Display", "SF Pro Icons", "Helvetica Neue", "Helvetica", "Arial", sans-serif;
    width: 430px;
    /* (430/1425)*100 = 30.175438596491226 */
    width: 30.175438596491226vw;
    font-size: 21px;
    /* (21/768)*100 */
    font-size: 2.734375vh;
    color: #1d1d1f;
    font-weight: 400;
    line-height: 26px;
    /* (26/768)*100 */
    line-height: 3.385416666666667vh;
    margin: 0 auto;
    margin-top: 4px;
}
.apps h5+div {
    margin: 0 auto 0;
    margin-top: 64px;
    /* (64/768)*100 */
    margin-top: 8.333333333333333vh;
}

/* 추가 가변형 이미지 */
.apps li:nth-child(1) div,
.apps li:nth-child(2) div {
    width: 200px;
    width: 14.035087719298245vw;
    height: 200px;
    height: 26.041666666666668vh;
    height: auto;
}
.apps li:nth-child(3) div {
    width: 576px;
    width: 40.421052631578945vw;
    height: 336px;
    height: auto;
}
.apps li:nth-child(4) div {
    width: 191px;
    width: 13.403508771929825vw;
    height: 330px;
    height: auto;
}
.apps li:nth-child(5) div {
    width: 151px;
    width: 10.596491228070175vw;
    height: 300px;
    height: auto;
}
.apps li:nth-child(6) div {
    width: 439px;
    width: 30.80701754385965vw;
    height: 318px;
    height: auto;
}
/* 각각 이미지 부모박스에 맞추기 */
.apps h5+div img {
    width: 100%;
    height: 100%;
}

