/* HomePage */
.homepage {
    color: #fff;
    padding-bottom: 70px;
    /* (70/768)*100 */
    padding-bottom: 9.114583333333332vh;
}
/* 첫번째 li */
/* 리액트 배경이미지 빌드 수정 */
.homepage .main_img .first {
    /* background-image: url("../images/wwdc21-hero3- .jpeg"); */
    background-repeat: no-repeat;
    background-position: center;
    background-size: 100% 624px;
    background-size: 100% 81.25vh;
    height: 624px;
    /* (624/768)*100 = 81.25 */
    height: 81.25vh;
    /* 위치기준 */
    position: relative;
}
