    position: relative;
} */

.homepage .main_img li:first-child .main_img_text {
    /* 포지션 */
    position: absolute;
    left: 100px;
    /* (100/1425)*100 = 7.017543859649122 */
    left: 7.017543859649122vw;
    /* (150/768)*100 = 19.53125 */
    top: 19.53125vh;
    z-index: 100;
    padding-top: 88px;
    /* (88/768)*100 = 11.458333333333332 */
    padding-top: 11.458333333333332vh;
}
.homepage .main_img h3>img {
    width: 50%;
}
.homepage .main_img li:first-child h4 {
    visibility: hidden;
}
.homepage .main_img li:first-child h5 {
    font-size: 27px;
    /* (27/768)*100 = 3.515625 = 11.458333333333332 */
    font-size: 3.515625vh;
    font-weight: 400;
    color: #f5f5f7;
    line-height: 32px;
    /* (32/768)*100 = 4.16666666666666 */
    line-height: 4.166666666666666vh;
}
/* 두번째 li */
.homepage .main_img li:last-child {
    text-align: center;
    margin-top: 80px;
    padding-top: 11.458333333333332vh;
}
.homepage .main_img li:last-child h3 {
    color: #000;
    font-size: 44px;
    /* (44/768)*100 = 5.729166666666666 */
    font-size: 5.729166666666666vh;
    font-weight: 600;
    line-height: 48px;
    /* (48/768)*100 = 6.25 */
    line-height: 6.25vh;
    margin-bottom: 48px;
    /* (48/768)*100 = 6.25 */
    margin-bottom: 6.25vh;
}
.homepage .main_img li:last-child h4>img {
    width: 635px;
    /* (635/1425)*100 = 44.56140350877193 */
    width: 44.56140350877193vw;
}