/* Header 가변 기준값 1425x768 */
#header {
    background-color: rgba(240,240,240,0.8);
    position: sticky;
    top: 0;
    z-index: 100;
}
/* 상단 GNB 메뉴 */
#header nav {
    width: 980px;
    /* (980/1425)*100 */
    width: 68.77192982456141vw;
    margin: 0 auto;
}
#header .gnb {
    height: 44px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}
#header .gnb img {
    margin-top: -4px;
    margin-right: 4px;
}
#header .gnb strong {
    display: inline-block;
    font-size: 1rem;
    font-weight: 600;
    margin-top: 6px;
}
/* https://codepen.io/sosuke/pen/Pjoqqp = filter값 얻기 */
.svg,
.search {
    width: 17px;
    width: 1.1929824561403508vw;
    filter: invert(0%) sepia(95%) saturate(21%) hue-rotate(16deg) brightness(95%) contrast(105%);
}