.platforms {
    max-width: 100%;
    height: 320px;
    /* (320/768)*100 */
    height: 41.666666666666667vh;
    background: #fafafa;
    padding: 12px 0;
    /* (12/768)*100 */
    padding: 1.5625vh 0;
    margin-bottom: 12px;
    /* (12/768)*100 */
    margin-bottom: 12px;
    margin-bottom: 1.5625vh;
    text-align: center;
}
.platforms h3 {
    color: #1d1d1f;
    font-size: 32px;
    /* (32/768)*100 */
    font-size: 4.166666666666667vh;
    line-height: 1.125;
    font-weight: 600;
    letter-spacing: .004em;
    font-family: "SF Pro Display", "SF Pro Icons", "Helvetica Neue", "Helvetica", "Arial", sans-serif;
    padding: 25px 0;
    /* (25/768)*100 */
    padding: 3.255208333333333vh 0;
}
.platforms .links {
    /* width: 980px; */
    max-width: 85.96491228070175vw;
    margin: 0 auto;
    display: flex;
    justify-content: center;
    /* 내용 아이템들 교차축 정렬 */
    align-items: flex-end;
}
.platforms .links a {
    margin: 0 28px;
    /* (28/1425)*100 = 1.9649122807017545 */
    margin: 0 1.9649122807017545vw;
    display: flex;
    flex-direction: column;
}
.platforms .links a p:last-child {
    margin-top: 8px;
    /* (8/768)*100 */
    margin-top: 1.041666666666667vh;
}

/* 리액트 추가 = 아이콘 이미지 가변형 */
.platforms .links a img {
    width: 100%;
    height: 100%;
}