.banner {
    /* 리액트 코드 추가 */
    width: 100%;
    height: 560px;
    background: linear-gradient(rgb(0,0,0) 0%, rgb(15,114,197) 100%);
    margin-bottom: 12px;
}
.banner .section_content {
    width: 980px;
    /* (980/1425)*100 = 68.77192982456141 */
    width: 68.77192982456141vw;
    margin: 0 auto;
    text-align: center;
    color: #fff;
}
.banner .section_content div {
    width: 520px;
    /* (520/1425)*100 = 36.49122807017544 */
    width: 36.49122807017544vw;
    margin: 0 auto;
    padding: 36px;
    /* (36/560)*100 = 6.428571428571428 */
    /* (36/1425)*100 = 2.526315789473684 */
    padding: 6.428571428571428vh 2.526315789473684vw;
    letter-spacing: -1px;
}
.banner .section_content div h3,
.banner .section_content div h5 {
    font-size: 32px;
    color: #f5f5f7;
    line-height: 1.125;
    font-weight: 600;
}
.banner .section_content div h5 {
    font-size: 18px;
    font-size: 1.125rem;
    font-weight: 400;
    line-height: 1.25;
    margin-top: 8px;
    /* (8/560)*100 = 1.4285714285714286 */
    margin-top: 1.4285714285714286vh;
}

/* 리액트 배경이미지 수정예정 */

.banner .banner_bg {
    height: 376px;
    height: 48.95833333333333vh;
    /* background: url(../images/build-apps_2x.png) no-repeat center; */
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    margin-top: -50px;
    margin-top: -9vh;
}
