.aside {
    background-color: #f5f5f7;
    padding: 25px 0;
    /* (25/768)*100 */
    padding: 3.255208333333333vh 0;
}
.aside .section_content {
    /* width: 980px; */
    /* (980/1425)*100 = 68.77192982456141 */
    max-width: 68.77192982456141vw;
    margin: 0 auto;
    color: #1d1d1f;
    font-size: 17px;
    font-size: 2.21vh;
    /* 플렉스 */
    display: flex;
    flex-basis: 25%;
}
.aside .section_content div {
    text-align: center;
}
.aside .section_content div figure {
    width: 50px;
    height: 50px;
    /* background: url('../svg/news.svg') no-repeat center; */
    background-repeat: no-repeat;
    background-position: center;
    background-size: 100%;
    margin: 4px auto 10px;
}
