.aside .section_content div:nth-child(3) figure {
    background: url("../svg/download.svg");
}
.aside .section_content div:nth-child(4) figure {
    background: url("../svg/program.svg");
} */

.aside .section_content div p {
    width: 216px;
    /* (216/1440)*100 = 15 */
    width: 15vw;
    color: #1d1d1f;
    font-size: 17px;
    /* (17/768)*100 = 2.213541666666667 */
    font-size: 2.213541666666667vh;
    font-weight: 600;
    line-height: 25px;
    /* (25/768)*100 = 3.2552083333333335 */
    line-height: 3.2552083333333335vh;
}
.aside .section_content div p:nth-last-child(2),
.aside .section_content div p:last-child {
    font-size: 14px;
    /* (14/768)*100 = 1.8229166666666667 */
    font-size: 1.8229166666666667vh;
    font-weight: 400;
    margin-top: 12px;
    /* (12/768)*100 = 1.5625 */
    margin-top: 1.5625vh;
    line-height: 20px;
    /* (20/768)*100 = 3.2552083333333335 */
    line-height: 3.2552083333333335vh;
}
.aside .section_content div p:last-child {
    color: #06c;
    margin-top: 12px;
    /* (12/768)*100 = 1.5625 */
    margin-top: 1.5625vh;
}
.aside .section_content div span::after {
    content: "\f054";
    font-family: "Font Awesome 5 Free";
    font-weight: 900;
    font-size: 13px;
    /* (13/768)*100 = 1.6927083333333333 */
    font-size: 1.6927083333333333vh;
    color: #06c;
    padding-left: 5px;
    display: inline-block;  
}